// Style for the Quiz block
.gtb-quiz {
	$self: &;

	&__answer {
		position: relative;
	}

	&__remove-question {
		position: absolute;
		right: 0;
		top: -8px;
		height: auto;
		padding: 0;
	}

	&__remove-answer {
		padding: 0;
		height: auto;
		position: relative;
		top: 0.5rem;
	}

	&__correct-answer {
		margin-block: 1.5rem;
	}
}